import {React, useEffect, useState} from 'react';
import { useNavigate,useLocation } from 'react-router-dom';
import { useFormik } from 'formik';
import '../../../../styles/Paginas/Preparo/RecebimentoStyle.css'
import API from '../../../../Services/index';
import PageWrapper from 		'../../../../layout/PageWrapper/PageWrapper';
import Page from 				'../../../../layout/Page/Page';
import { cirurgicalFlow } from 	'../../../../menu';
import Header from '../../../../components/Header';
import Modal, {ModalBody,ModalFooter,ModalHeader,ModalTitle} from '../../../../components/bootstrap/Modal';
import  {DropdownItem, DropdownMenu, DropdownToggle} from '../../../../components/bootstrap/Dropdown';
import Dropdown from '../../../../components/bootstrap/Dropdown';
import Button, {ButtonGroup} from '../../../../components/bootstrap/Button';
import Card, {CardBody,CardHeader,CardTitle} from '../../../../components/bootstrap/Card';
import Icon from '../../../../components/icon/Icon';
import './style.css'


const Recebimento = () => {


const location = useLocation()
const navigate = useNavigate()

	
/*
*	States
*/

const [Checklist, setChecklist] = useState([])
// eslint-disable-next-line no-unused-vars
const [questions, setQuestions] = useState([])


/** 
 * novas
 */

const [ModalCancelamentoCirurgia, 		setModalCancelamentoCirurgia] 		= useState(false)
const [MotivosCancCir, setMotivosCancCir] = useState([])
// eslint-disable-next-line no-unused-vars
const [motivoCancelamentoCirurgia, setMotivoCancelamentoCirurgia] = useState(null)

const [ModalHorarioAlert, 			setModalHorarioAlert] 				= useState(false)
const [ModalCamposObrigatorios, 	setModalCamposObrigatorios] 		= useState(false)
const [ModalConfirmDesfazerEtapa, 	setModalConfirmDesfazerEtapa] 		= useState(false)


useEffect(() => {
		 PerguntasChecklist( location.state.INFO_PROX_ETAPA.PROX_ETAPA.CD_ETAPA, location.state.INFO_PROX_ETAPA.PROX_ETAPA.COD_AVISO)

}, [location])


const PerguntasChecklist = async ( paramCdProxEtapa, paramCdAviso ) => {
	try {
		const question = await API.get(`/listaOpcoesCheckListPreparo?cd_etapa=${paramCdProxEtapa}&cd_aviso=${paramCdAviso}`);
		
		/*
			console.log(question.data)
			const response = JSON.stringify(question.data);
		*/
		
		setQuestions(question.data.sort((a,b)=>a.CD_PERGUNTA - b.CD_PERGUNTA))
		/*
		setQuestions(arr=>[...arr, JSON.parse(response)])
		*/

	} catch (error) {
		console.log(error);
	}
}

const [QuantOpSelecionada, setOpcoesSelecionadas] = useState(0)

// eslint-disable-next-line no-unused-vars
const SelecionarOpcao = async (CodigoDaPergunta, Pergunta, CodigoReposta, respostaSelecionada, tipoOpcao, CodigoAviso) => {
	/**
	* Joga as opções selecionadas dentro de um array de objetos.
	*/

	setChecklist(arr=>[...arr, {
					'CdPergunta': 	CodigoDaPergunta, 
					'Pergunta': 	Pergunta, 
					'Resposta': 	respostaSelecionada,
					'CodResposta': 	CodigoReposta,
					'TpCampo': 		tipoOpcao,
					'CodAviso': 	CodigoAviso,
					'Usuario': 		''
	}])

	setOpcoesSelecionadas(QuantOpSelecionada + 1)
			
	return true
}; 

/**
* Header
*/

const alterarLocalidade = () => {
	localStorage.removeItem('CAPP-local');
	navigate('/')
};


/**
 * Método chamado após clicar no botão de prosseguir.
 */
const Next = async() => {
	/**
	 * Verifica se a data de jejum é superior que a data atual.
	 */
	try {
		const requestDataAtual 	= await API.get('/retornaDataAtual');
		const responseDataAtual = requestDataAtual.data.DATA;
		
		const inputInicioJejum = new Date((document.getElementById("data").value)).getTime()
		const dataAgora		   = new Date(responseDataAtual).getTime()

		if(inputInicioJejum > dataAgora){
			setModalHorarioAlert(true)
			return;
		}
		
	} catch (error) {
		console.log(error)
	}

	
	
	
	if(QuantOpSelecionada >= questions.length)
		{
			navigate('/salaPreparo/assinatura', {
				state: {
					CODIGO_LOCALIDADE: location.state.CODIGO_LOCALIDADE,
					CODIGO_SUB_LOCALIDADE: location.state.CODIGO_SUB_LOCALIDADE,
					DADOS: location.state.DADOS,
					INFO_PROX_ETAPA: {
						PROX_ETAPA: location.state.INFO_PROX_ETAPA.PROX_ETAPA
					},
					CHECKLIST: Checklist
				}
			  });
		}else{
			setModalCamposObrigatorios(true)

	}
};


const formik = useFormik({
		initialValues: {
			eventStart: '15:51:00'
		},
		onSubmit: () => {
			/*
			console.log(values.eventStart);
			*/
			formik.setValues({});
		}
});




const retornaMotivosCancCir = async () => {
	const consulta = await API.get(`/motivosCancelamentoCirurgia`);
	setMotivosCancCir(consulta.data)
	
}

return (
		<PageWrapper title={cirurgicalFlow.PREPARO_RECEBIMENTO.text}>
			<Header titulo='Sala de Preparo' localidade='Checklist'>
				<Dropdown >
					<DropdownToggle>
					<Button
							color="light"
							icon="menu"
							isLight
							id="menu"
						>
							<p> </p>
						</Button>
					</DropdownToggle>
					<DropdownMenu>
						<DropdownItem onClick={() => alterarLocalidade()}>
							Alterar Local
						</DropdownItem>
						<DropdownItem  onClick={() => {
						retornaMotivosCancCir()
						setModalCancelamentoCirurgia(true)
						document.getElementById("menu").click();
						}}>
                         Suspender Cirurgia
                    </DropdownItem>
				    {
						location.state.DADOS.CD_ETAPA >= 20 && 
						<DropdownItem onClick={()=> setModalConfirmDesfazerEtapa(true)}> 
							Desfazer etapa do Paciente 
						</DropdownItem>
					
					}
					</DropdownMenu>
				</Dropdown>
			</Header>
			<Page>
				<br />
				<div>
					<Card>
						<CardHeader className='header-card-paciente'>
							<CardTitle className='header-nome-paciente'>
								{ location.state.DADOS.PACIENTE }
							</CardTitle>
						</CardHeader>
						<CardBody>
							<div className='bloco-info-paciente-preparo' style={{"backgroundColor": "#fff !important"}}>
								<div>
									<Icon icon='person' size='8x' forceFamily='material' /><br />
									{ location.state.DADOS.NM_PRESTADOR }
								</div>
								<div style={{"width": "600px"}}>
									<Icon icon='health_and_safety' size='8x' forceFamily='material' /><br />
									{ location.state.DADOS.CIRURGIA }
								</div>
								<div>
									<Icon icon='schedule' size='8x' forceFamily='material' /><br />
									{ location.state.DADOS.PREVISAO_INICIO }
								</div>
							</div>
						</CardBody>

					</Card>
				</div>

				<div>
					{
					/**
					 * [TESTE] Só mostra o checklist, se != transferir pro C.C
					 * [PENDENTE] Criação de perguntas pra esta etapa, para realizar devido testes;
					location.state.DADOS.CD_ETAPA !== 20 &&
					*/
					

					<Card>
						<CardHeader className='header-card-checklist'>
							<CardTitle className='header-checklist'>
								Checklist:
							</CardTitle>
						</CardHeader>
						<CardBody>
						<div>
							<PageWrapper>
									<Page>
									<table className='table-modern '>
										<tbody>
											{
											questions.length ?
												questions.map((i) => {
													return (
														<tr key={i.CD_PERGUNTA}>
																<td className='fw-bold'>
																	<h2>{ i.DS_PERGUNTA }</h2>
																</td> 
																{

																			i.PERGUNTAS.map((j) => {
																				return (
																					<th scope='row'>	
																					{
																						j.TP_CAMPO === 'RADIO' && 
																							<label>
																								<input type='radio' className='radio-preparo' style={{ "border": `10px solid #${j.COR}` }} id={j.CD_RESPOSTA} name={i.CD_PERGUNTA} value={j.DS_RESPOSTA} onChange={(e)=> SelecionarOpcao(i.CD_PERGUNTA, i.DS_PERGUNTA, j.CD_RESPOSTA, e.target.defaultValue, 'RADIO', location.state.DADOS.COD_AVISO)} />
																								<div className='ds-resposta'>
																									{j.DS_RESPOSTA}
																								</div>
																							</label>
																																																														
																					}																					  
																					{
																						/**
																						* Se o tipo de pergunta for do tipo TIME, será monstrado da seguinte forma:
																						*/
																							
																						j.TP_CAMPO === 'TIME' && 
																						<input className='input-time' type="time" id="data" name="date"  onChange={(e)=> SelecionarOpcao(null, 'Início Jejum', null, e.target.value, 'DATATIME', location.state.DADOS.COD_AVISO)}/> 																					
																					}

																					{
																						/**
																						* Se o tipo de pergunta for do tipo TIME, será monstrado da seguinte forma:
																						*/
																							
																						j.TP_CAMPO === 'DATATIME' && 
																						<input className='input-datatime' max={ new Date().toISOString().slice(0,new Date().toISOString().lastIndexOf(":")) } style={{"maxWidth": "250px"}} type="datetime-local" id="data" name="date"  onChange={(e)=> SelecionarOpcao(i.CD_PERGUNTA, 'Início Jejum', j.CD_RESPOSTA, e.target.value, 'DATATIME', location.state.DADOS.COD_AVISO)}/> 																					
																					}

																						

																					</th>
																				);																				
																			})

																		}
																																			
																	</tr>
																)
															})
															:
															<p>Carregando...</p>

												}
											</tbody>
										</table>
									</Page>
								</PageWrapper>
							</div>
							
						</CardBody>
					</Card>

											}
					
					<div className='btn-continue'>
						{
							location.state.DADOS.CD_ETAPA !== 20 ?
								<Button
								color="success"
								className="btn-sucess"
								onClick={() => Next()}
								>
								<Icon icon='check' color= '#fff' size='6x' forceFamily= 'material' />
								</Button>
							:
								<Button onClick={() => Next()} color="dark">
									<Icon    icon='Logout' color= '#fff' size='3x' forceFamily= 'material' />
									<h3>Sala C.C</h3>
								</Button>
						}	
					
					</div>	 
					
					
					<div className='btn-back-checklist'>
					<Icon   onClick={() => window.history.go(-1)}
							icon='reply' 
							color= '#fff' 
							size='10x' 
							forceFamily= 'material' 
					/>

				</div>
				</div>
						

				<Modal
					id='modalCancelamentoCirurgia'
					isOpen={ModalCancelamentoCirurgia}
					titleId='modalCancelamentoCirurgia'>
						<ModalHeader style={{"backgroundColor":"#FECE64"}}>
							<ModalTitle style={{"color": "#fff"}} id='example-title'> 
								Confirma a Suspensão da Cirurgia?
							</ModalTitle>
						</ModalHeader>
						<ModalBody className='ModalBody'>
							<h3>Informe o motivo:</h3><br />
							<select onChange={(e)=>setMotivoCancelamentoCirurgia(e.target.value)} name="motivos" id="motivos">
								<option value="Selecione">Selecione</option>
								{
									MotivosCancCir.map((i) => {
										return (
											<option key={i.CD_MOT_CANC} value={i.CD_MOT_CANC}>{i.DS_MOT_CANC}</option>
										)
									})

								}
							</select>																	
						</ModalBody>
						<ModalFooter>
							<ButtonGroup>
									<Button className='btn-style' color='light'  onClick={()=> {
										setModalCancelamentoCirurgia(false)
									}}>
										Cancelar
									</Button>
									<Button onClick={()=> {
										// eslint-disable-next-line no-unused-expressions
										motivoCancelamentoCirurgia === null ?
											alert('Preencha o motivo da suspensão da cirurgia antes de concluir.')
										:
										navigate('/salaPreparo/AssinaturaAlteracoes', {
											state: {
												TP_ACAO: 'CANCELAR_CIRURGIA',
												COD_AVISO: location.state.DADOS.COD_AVISO,
												MOTIVO: motivoCancelamentoCirurgia,
												DADOS: { DADOS: location.state.DADOS }
											}
										});
									}} className='btn-style' color='success'>
											Confirmar
									</Button>
							</ButtonGroup>
						</ModalFooter>
				</Modal>		
				<Modal
					id='modalHorarioJejum'
					isCentered
					isOpen={ModalHorarioAlert}
					titleId='modalHorarioJejum'>
						<ModalHeader style={{"backgroundColor":"#FECE64"}}>
							<ModalTitle style={{"color": "#fff"}}> 
								ALERTA
							</ModalTitle>
						</ModalHeader>
						<ModalBody className='ModalBody'>
							<h3>O horário de início de jejum não pode ser superior que o horário atual.</h3><br />
						</ModalBody>
						<ModalFooter>
							<ButtonGroup>
									<Button className='btn-style' color='light'  onClick={()=> {
										setModalHorarioAlert(false)
									}}>
										FECHAR
									</Button>
							</ButtonGroup>
						</ModalFooter>
				</Modal>	


				<Modal isCentered isOpen={ModalConfirmDesfazerEtapa} isAnimation={false} is>
						<ModalHeader style={{"backgroundColor":"#FECE64"}}>
							<ModalTitle style={{"color": "#fff"}}> 
								ALERTA
							</ModalTitle>
						</ModalHeader>
						<ModalBody className='ModalBody'>
							<h3>Você realmente confirma desfazer etapa do Paciente?</h3><br />
						</ModalBody>
						<ModalFooter>
							<Button className='btn-style' size='lg' color='light'  onClick={()=> {setModalConfirmDesfazerEtapa(false)}}>
								FECHAR
							</Button>
							<Button className='btn-style' size='lg' color='success'  onClick={()=> { 
								navigate('/salaPreparo/AssinaturaDesfazerEtapa', {
									state: {
										DADOS: location.state.DADOS
									}
								});
							}	
							}>
								CONFIRMAR
							</Button>
						</ModalFooter>
				</Modal>	
				
				<Modal
					id='modalCamposObrigatorios'
					isCentered
					isOpen={ModalCamposObrigatorios}
					titleId='modalCamposObrigatorios'>
						<ModalHeader style={{"backgroundColor":"#FECE64"}}>
							<ModalTitle style={{"color": "#fff"}}> 
								ALERTA
							</ModalTitle>
						</ModalHeader>
						<ModalBody className='ModalBody'>
							<h3>Informe o início de jejum do paciente.</h3><br />
						</ModalBody>
						<ModalFooter>
							<ButtonGroup>
									<Button className='btn-style' color='light'  onClick={()=> {
										setModalCamposObrigatorios(false)
									}}>
										FECHAR
									</Button>
							</ButtonGroup>
						</ModalFooter>
				</Modal>	


					
		</Page>
			
		</PageWrapper>
	);
};

export default Recebimento;